.GameSummary {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__header {
    font-weight: 400;
    font-size: 25px;
    margin: 30px 0 30px;
    &--addPlayers,
    &--chooseGame {
      font-size: 22px;
      font-weight: 300;
      margin: 0 0 20px;
    }
  }
  &__addPlayer {
    width: 50px;
    height: 50px;
    margin-bottom: 20px;
    background-color: $box-color;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $font-color;
    border-radius: 100%;
    font-size: 30px;
    font-weight: 800;
    transition: transform 0.2 ease;
    &:active {
      transform: scale(1.05);
    }
  }
  &__usersPrompt {
    width: 80%;
    height: 200px;
    background-color: $box-color;
    position: absolute;
    top: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    left: 50%;
    border-radius: 10px;
    transform: translate(-50%, -50%);
  }
  &__usersPromptUsers {
    width: 100%;
  }
  &__usersPromptAdd {
    font-size: 22px;
    letter-spacing: 1px;
    width: 90%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.24);
    color: $font-color;
    border-radius: 30px;
  }
  &__usersPromptLabel {
    font-size: 25px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    outline: none;
  }
  &__usersPromptSelect {
    width: 90%;
    border-radius: 10px;
    margin-top: 20px;
    background-color: $box-color;
    color: $font-color;
  }
  &__usersIconWrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }
  &__playerIcon {
    width: 80px;
    height: 80px;
    border-radius: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border: 3px solid $second-color;
    margin: 10px 10px 30px;
  }
  &__playerIconImg {
    width: 100%;
    height: 100%;
  }
  &__currentGameWrapper {
    font-size: 25px;
    color: $second-color;
    margin: 10px 0 30px;
  }

  // >>>>>>>>>> POINTS DISTRIBUTION <<<<<<<<<<<

  &__addPointsUl {
    width: 100%;
  }
  &__addPointsLi {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid $second-color;
    &:last-child {
      border: none;
    }
  }
  &__addPointsImageWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    overflow: hidden;
    width: 60px;
    height: 60px;
  }
  &__addPointsImage {
    width: 100%;
    height: 100%;
  }
  &__addPointsPlace {
    font-size: 25px;
  }
  &__addPointsPointsForPlace {
    font-size: 20px;
  }
  &__addPointsNicknme {
    font-size: 20px;
  }
  &__addPointsAddAnother {
    width: 50%;
    padding: 10px;
    font-size: 22px;
    border-radius: 20px;
    background-color: $box-color;
    text-align: center;
    margin: 10px 0 20px;
  }
}
