.Buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__header {
    font-weight: 400;
    font-size: 25px;
    margin: 30px 0;
  }
  &__addUser,
  &__addGame {
    width: 70%;
    background-color: $box-color;
    font-size: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    height: 50px;
    border-radius: 30px;
    color: $font-color;
  }
}
