.Profile {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 10px;
  &__header,
  &__headerGames {
    font-size: 25px;
    font-weight: 300;
    margin-bottom: 40px;
  }
  &__photoAndCirclesWrapper {
    position: relative;
  }
  &__photoWrapper {
    width: 130px;
    height: 130px;
    overflow: hidden;
    border-radius: 100%;
    border: 4px solid $second-color;
    box-shadow: 0 0 80px 10px rgba(15, 190, 68, 0.438);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__circle {
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: rgba(156, 156, 156, 0.144);
    border-radius: 100%;
    &--1 {
      right: -70px;
      top: 30%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 10px;
      height: 10px;
    }
    &--2 {
      left: -55px;
      top: 35%;
      background-color: rgba(156, 156, 156, 0.151);
      width: 10px;
      height: 10px;
    }
    &--3 {
      left: -35px;
      top: 20%;
      background-color: rgba(156, 156, 156, 0.192);
      width: 10px;
      height: 10px;
    }
    &--4 {
      right: -30px;
      top: 30%;
      background-color: rgba(156, 156, 156, 0.295);
    }
    &--5 {
      right: -40px;
      top: 50%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 10px;
      height: 10px;
    }
    &--6 {
      right: -6px;
      top: 5%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 9px;
      height: 9px;
    }
    &--7 {
      right: -20px;
      top: 75%;
      background-color: rgba(156, 156, 156, 0.144);
      width: 8px;
      height: 8px;
    }
    &--8 {
      right: -50px;
      top: 15%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 10px;
      height: 10px;
    }
    &--9 {
      right: -90px;
      top: 55%;
      background-color: rgba(156, 156, 156, 0.164);
      width: 8px;
      height: 8px;
    }
    &--10 {
      right: -70px;
      top: 65%;
      background-color: rgba(156, 156, 156, 0.233);
      width: 11px;
      height: 11px;
    }
    &--11 {
      right: -70px;
      top: 45%;
      background-color: rgba(156, 156, 156, 0.075);
      width: 8px;
      height: 8px;
    }
    &--12 {
      right: -85px;
      top: 35%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 8px;
      height: 8px;
    }
    &--13 {
      right: -25px;
      top: 55%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 8px;
      height: 8px;
    }

    &--14 {
      left: -70px;
      top: 30%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 10px;
      height: 10px;
    }
    &--15 {
      right: -60px;
      top: 50%;
      background-color: rgba(156, 156, 156, 0.151);
    }
    &--16 {
      right: -35px;
      top: 20%;
      background-color: rgba(156, 156, 156, 0.192);
      width: 10px;
      height: 10px;
    }
    &--17 {
      left: -30px;
      top: 30%;
      background-color: rgba(156, 156, 156, 0.295);
    }
    &--18 {
      left: -40px;
      top: 50%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 10px;
      height: 10px;
    }
    &--19 {
      left: -6px;
      top: 5%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 9px;
      height: 9px;
    }
    &--20 {
      left: -20px;
      top: 75%;
      background-color: rgba(156, 156, 156, 0.144);
      width: 8px;
      height: 8px;
    }
    &--21 {
      left: -50px;
      top: 15%;
      background-color: rgba(156, 156, 156, 0.171);
      width: 10px;
      height: 10px;
    }
    &--22 {
      left: -90px;
      top: 55%;
      background-color: rgba(156, 156, 156, 0.164);
      width: 8px;
      height: 8px;
    }
    &--23 {
      left: -70px;
      top: 65%;
      background-color: rgba(156, 156, 156, 0.233);
      width: 11px;
      height: 11px;
    }
    &--24 {
      left: -70px;
      top: 45%;
      background-color: rgba(156, 156, 156, 0.075);
      width: 8px;
      height: 8px;
    }
    &--25 {
      left: -85px;
      top: 35%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 8px;
      height: 8px;
    }
    &--26 {
      left: -25px;
      top: 55%;
      background-color: rgba(156, 156, 156, 0.055);
      width: 8px;
      height: 8px;
    }
  }
  &__image {
    width: 100%;
  }
  &__name {
    font-size: 23px;
    font-weight: 300;
    margin: 10px 0 5px;
  }
  &__points {
    font-size: 27px;
    color: $second-color;
  }
  &__headerGames {
    margin: 20px 0;
  }

  /* >>>>>>>>>>>>>>> HISTORY OF GAMES <<<<<<<<<<<<<<<<<<<<< */

  &__gameAllWrapper {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 20px;
  }
  &__gamePlace {
    font-size: 30px;
    color: #ffd700;
  }
  &__gameWrapper {
    display: grid;
    width: 80%;
    grid-template: 1fr 1fr / repeat(4, 1fr);
    background-color: $box-color;
    border-radius: 50px;
    overflow: hidden;
  }
  &__logoWrapper {
    width: 55px;
    height: 55px;
    border-radius: 100%;
    overflow: hidden;
    grid-row: 1 / 3;
  }
  &__logoImage {
    width: 100%;
  }
  &__gameName {
    grid-row: 1;
    grid-column: 2 / 5;
    font-size: 23px;
    align-self: center;
    justify-self: center;
    color: $second-color;
    padding-right: 30px;
  }
  &__gameDate {
    grid-column: 2 / -1;
    grid-row: 2;
    align-self: center;
    justify-self: center;
    font-size: 20px;
    padding-right: 30px;
  }
}
